import React, { Component } from 'react';
import idb from 'idb';
import logo from './logo.svg';
import './App.css';

class App extends Component {

	constructor(props) {
		super(props);
		this.state = {thename: '', displayname: '', timeEntered: ''};
		this.saveDownName = this.saveDownName.bind(this);
		this.setTheName = this.setTheName.bind(this);

		idb.open('mydb', 1, (upgradeDB) => {
			upgradeDB.createObjectStore('names');
		})
		.then(db => {
			db.transaction('names')
				.objectStore('names')
				.getAll()
			.then(objects => {
				this.setState({displayname: objects[0], timeEntered: objects[1]});
			});
		}) 

	}	

	setTheName(event) {
		this.setState({thename: event.target.value});
	}

	saveDownName(event) {
		var displayname = this.state.thename;
		var timeEntered = JSON.stringify(new Date());

		idb.open('mydb', 1, (upgradeDB) => { }).then(db => {
			console.log('db open');
			const val = displayname;
			const key = 'name';

			const tx = db.transaction('names', 'readwrite');
			tx.objectStore('names').put(val, key);

			const val2 = timeEntered;
			const key2 = 'timeEntered';

			const tx2 = db.transaction('names', 'readwrite');
			tx2.objectStore('names').put(val2, key2);

			return (tx.complete && tx2.complete);
		});
		this.setState({displayname: displayname});
		this.setState({timeEntered: timeEntered});
	}

	render() {
		return (
			<div className="App">
				<header className="App-header">
					<p> Here is an indexed db app for practice </p>
					<img src={logo} className="App-logo" alt="logo" />
					<p>
						name: {this.state.displayname} 
						<br />
						last updated: {this.state.timeEntered}
						<br />
						<br />
						<br />
						put in a name:
						<br /> 
						<input className="App-nametest" type="text" onChange={this.setTheName} />
						<br />
						<button className="App-submit" onClick={this.saveDownName}>save</button>
					</p>
				</header>
			</div>
		);
	}
}

export default App;
